nsform:translate(-2px, 0)}4%,60%{-webkit-transform:translate(-2px, 0);transform:translate(-2px, 0)}62%{-webkit-transform:translate(-22px, 5px) skew(21deg);transform:translate(-22px, 5px) skew(21deg)}}@keyframes glitchBotom{2%,64%{-webkit-transform:translate(-2px, 0);transform:translate(-2px, 0)}4%,60%{-webkit-transform:translate(-2px, 0);transform:translate(-2px, 0)}62%{-webkit-transform:translate(-22px, 5px) skew(21deg);transform:translate(-22px, 5px) skew(21deg)}}
